import React, { useState, useEffect } from "react";
import useDropdown from "./useDropdown";
import PetApi, { ANIMALS } from "./PetApi";
import Results from "./Results";

const SearchParams = () => {
  const [location, setLocation] = useState("Seattle, WA");
  const [breeds, updateBreeds] = useState([]);
  const [animal, AnimalDropdown] = useDropdown("Animal", "dog", ANIMALS);
  const [breed, BreedDropdown, updateBreed] = useDropdown("Breed", "", breeds);
  const [pets, setPets] = useState([]);
  const petApi = PetApi({
    key: "41OINr4RxiBSHshGvBe4cVqaWTeRArt4iZuszgwiZCj0YEBSMe",
    secret: "l0SvbkL5Whp5GSWU22fjVJutNJgyds22UpsX7dOb",
    version: "2"
  });

  async function requestPets() {
    const animals = await petApi.pet.find({
      location,
      breed,
      type: animal
    });
    console.log(animals.petfinder.pets.pet);
    setPets(animals.petfinder.pets.pet || []);
  }

  useEffect(() => {
    updateBreeds([]);
    updateBreed("");
    petApi.breed.list({ animal }).then(results => {
      const breedStrings = results.petfinder.breeds.breed;
      updateBreeds(breedStrings);
    }, console.error);
  }, [animal]);

  return (
    <div className="search-params">
      <form
        onSubmit={e => {
          e.preventDefault();
          requestPets();
        }}
      >
        <label htmlFor="location">
          Location
          <input
            id="location"
            value={location}
            placeholder="Location"
            onChange={e => setLocation(e.target.value)}
          />
        </label>
        <AnimalDropdown />
        <BreedDropdown />
        <button>Submit</button>
      </form>
      <Results pets={pets} />
    </div>
  );
};

export default SearchParams;
